<template>
    <div class="Login-content">
<!--        <Divider content-position="center" class="divider">用户登录</Divider>-->
        <label>
            <Input placeholder="用户名" v-model="user.username" class="Login-input"></Input>
        </label>
        <label>
            <Input placeholder="请输入密码" show-password v-model="user.password" class="Login-input"></Input>
        </label>
        <Button type="primary" round class="Login-button" @click="login">登录</Button>
    </div>
</template>

<script>
    import {Input, Button} from 'element-ui';
    export default {
        name: "loginForm",
        components: {Input, Button},
        data(){
            return{
                user: {
                    username: '',
                    password: ''
                }
            }
        },
        methods:{
            login(){
                console.log(this.$router.currentRoute.name);
                if(this.$router.currentRoute.name === 'login'){
                    this.$router.push({name: 'auth'});
                }else if(this.$router.currentRoute.name === 'adminLogin'){
                    this.$router.push({name: 'adminPage'});
                }
            }
        }
    }
</script>

<style scoped>
    .Login-content {
        padding: 0 40px 36px;
        font-size: 14px;
    }
    .Login-input >>> input {
        border-style: none;
        border-bottom: 1px solid #ebebeb;
        border-radius: 0;
        margin-top: 15px;
    }
    .Login-input >>> i {
        position: absolute;
        top: 8px;
        left: -30px;
    }
    .Login-button {
        margin-top: 30px;
    }
    .divider {
        margin: 4px 0 24px;
    }
</style>